.cardsWrapper{display:flex;justify-content:space-evenly;flex-wrap:wrap}.cardsWrapper .cardWr{display:flex;justify-content:space-between;display:flex;background-color:#29bbff;width:400px;height:50vh;margin:20px;position:relative}.cardsWrapper .cardWr .card{color:#fff;background-position:center;background-size:cover;transition:.1s;cursor:pointer}.cardsWrapper .cardWr .card:hover{scale:1.1}.cardsWrapper .cardWr .card.clouds{background-image:url("../images/clouds.jpg")}.cardsWrapper .cardWr .card.rain{background-image:url("../images/rain.jpg")}.cardsWrapper .cardWr .card.sunny{background-image:url("../images/sunny.jpg")}.cardsWrapper .cardWr .card .title{display:block;width:400px;text-align:center}.cardsWrapper .cardWr .card .temp{width:400px;text-align:center;margin-top:12%}.cardsWrapper .cardWr .card .temp .weatherStatus{font-size:24px;margin-bottom:5px;position:relative}.cardsWrapper .cardWr .card .temp .weatherStatus .weatherIcon{margin-left:6px;width:40px;height:40px;position:absolute;top:-4px}.cardsWrapper .cardWr .card .temp .currentTemp{font-size:56px;font-weight:bold;position:relative}.cardsWrapper .cardWr .card .temp .currentTemp::after{content:"°c";display:block;font-size:42px;position:absolute;top:0;right:31%}.cardsWrapper .cardWr .card .temp .minTemp{font-size:22px;color:#877f68;text-stroke:1px #fff;-webkit-text-stroke:1px #fff}.cardsWrapper .cardWr .minusImg{width:16px;height:16px;cursor:pointer;position:absolute;right:15px;top:15px}.cardsWrapper .cardWr .refresh{margin-top:20px;position:absolute;left:33%}.cardsWrapper .cardWr .empty{display:block;text-align:center}