.wrapper{display:flex;justify-content:center;align-items:center;position:fixed;inset:0;z-index:101}.wrapper .modalWr{background:#fff;z-index:100;width:600px;height:500px;background-position:center;background-size:cover}.wrapper .modalWr.clouds{background-image:url("../images/clouds.jpg")}.wrapper .modalWr.rain{background-image:url("../images/rain.jpg")}.wrapper .modalWr.sunny{background-image:url("../images/sunny.jpg")}.wrapper .modalWr .title{color:#fff;text-align:center}.wrapper .modalWr .temp{text-align:center;margin-top:10%}.wrapper .modalWr .temp .weatherStatus{font-size:24px;margin-bottom:5px;color:#fff;position:relative}.wrapper .modalWr .temp .weatherStatus .weatherIcon{margin-left:6px;width:40px;height:40px;position:absolute;top:-4px}.wrapper .modalWr .temp .currentTemp{font-size:56px;font-weight:bold;color:#fff;position:relative}.wrapper .modalWr .temp .currentTemp::after{content:"°c";display:block;font-size:42px;position:absolute;top:0;right:38%}.wrapper .modalWr .temp .minTemp{font-size:22px;color:#877f68;text-stroke:1px #fff;-webkit-text-stroke:1px #fff}.wrapper .forecastTitle{color:#fff;text-align:center;font-size:26px}.wrapper .blur{position:fixed;z-index:1;inset:0;width:100%;height:100%;background:#000;opacity:.4}